.chanels-section {
    margin: 100px auto 10px;
    @include lg {
        margin: 40px auto 0; }
    &__title {
        margin-bottom: 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        .title-big {
            padding-right: 50px; } }
    &__row {
        display: flex;
        flex-wrap: wrap;
        .card-online {
            min-width: 25%;
            @include md {
                @include flex-size(100%);
                margin-bottom: 24px; }
            @include sm {
                display: none; //MR
                margin-bottom: 12px; } }
        .chanel {
            flex-grow: 1; } }
    .chanel {
        &:not(:last-child) {
            margin-bottom: 32px; }
        @include md {
            margin-bottom: 24px; }
        @include sm {
            margin-bottom: 12px; } }
    &__load-more {
        padding: 5px 0 40px;
        text-align: center;
        @include md {
            padding: 5px 0 30px; } } }


