.tag {
    position: relative;
    display: inline-block;
    padding: 6px 10px;
    font-family: $ermilov;
    font-weight: 800;
    font-size: 36px;
    line-height: 1;
    letter-spacing: -0.06em;
    transition: color $tr;
    color: inherit;
    // overflow: hidden
    // pointer-events: none
    @include lg {
        font-size: 32px;
        padding: 1px 5px 3px; }
    @include sm {
        font-size: 24px; }
    @include hover {
        color: $white;
        @include sm {
            color: $black; } }
    &__text {
        position: relative;
        z-index: 3; }
    &__bg {
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-image: $mainGradient;
        z-index: 1;
        transition: left 0.4s, width 0.4s, color 0.4s;
        pointer-events: none;
        &:hover:after {
            @include fadein; }
        &:after {
            content: '';
            @include coverdiv;
            z-index: 2;
            background-image: $mainGradientReversed;
            opacity: 0;
            visibility: hidden;
            transition: opacity $tr, visibility $tr; } }
