 } }        //     +fadein



.tag-small {
    line-height: 1;
    display: inline-block;
    vertical-align: top;
    font-size: 14px;
    letter-spacing: -0.02em;
    position: relative;
    // +lg
    //     font-size: 20px
    // +sm
    //     font-size: 16px
    &:hover:after {
        @include fadein; }
    &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -4px;
        height: 2px;
        width: 100%;
        background-color: $white;
        @include fadeout($tr); } }


.chanel {
    position: relative;
    height: 210px;
    padding: 32px 0 18px;
    @include cover;
    color: $white;
    @include md {
        padding: 10px 18px; }
    &:after {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 40%;
        content: '';
        background-image: linear-gradient(90deg, rgba($black, 1) 0%, transparent 68.65%); }
    &__inner {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        // max-width: 1800px
        height: 100%;
        padding: 0 70px;
        margin: 0 auto;
        z-index: 5;
        @include lg {
            padding: 0 20px; }
        @include sm {
            flex-direction: column;
            align-items: flex-start; } }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        @include sm {
            margin-bottom: 16px; } }
    &__logo {
        @include circle(70);
        @include cover; }
    &__group {
        margin-bottom: 4px; //MR
        font-size: 12px;
        letter-spacing: -0.02em;
        line-height: 1.2;
        font-weight: 700; }
    &__name {
        font-size: 25px;
        line-height: 1;
        letter-spacing: -0.02em;
        margin-left: -0.08em;
        font-weight: 700; //MR
        @include sm {
            font-size: 18px; } }
    &__likes {
        margin-top: auto;
        .icon-heart {
            color: red; } }
    &__tags {
        line-height: 22px;
        & > * {
            margin-right: 3px; }
        // float: right
 } }        // display: flex

