.card-online {
    position: relative;
    height: 210px;
    color: $white;
    display: flex;
    align-items: center;
    padding: 0 130px 20px;
    overflow: hidden;
    @include hover {
        .card-online__bg {
            @include imgScale; } }
    &__bg {
        @include coverdiv;
        @include cover;
        backface-visibility: hidden;
        transition: transform 0.7s; }
    &__content {
        position: relative;
        min-width: 150px;
        .info-control {
            position: absolute;
            top: 100%;
            left: 10px; } }
    &__statistics {
        position: absolute;
        left: 130px;
        bottom: 10px; }
    .icon-heart {
        color: $white; } }
