@media only screen and (max-width: 1023px) { .tags { width: 100%; margin: 10px 0 0; } }

.tag { position: relative; display: inline-block; padding: 6px 10px; font-family: "Geometria", sans-serif; font-weight: 800; font-size: 36px; line-height: 1; letter-spacing: -0.06em; transition: color 0.4s; color: inherit; }

