.tag__text { position: relative; z-index: 3; }

.tag__bg { display: inline-block; position: absolute; top: 0; left: 0; height: 100%; width: 100%; background-image: linear-gradient(to right, #8200ff, #005eff); z-index: 1; transition: left 0.4s, width 0.4s, color 0.4s; pointer-events: none; }

.tag__bg:hover:after { opacity: 1; visibility: visible; }

.tag__bg:after { content: ''; position: absolute; top: 0; left: 0; width: 100%; height: 100%; z-index: 2; background-image: linear-gradient(to left, #8200ff, #005eff); opacity: 0; visibility: hidden; transition: opacity 0.4s, visibility 0.4s; }

