.tag-small:hover:after { opacity: 1; visibility: visible; }

.tag-small:after { content: ''; position: absolute; left: 0; bottom: -4px; height: 2px; width: 100%; background-color: #fff; opacity: 0; visibility: hidden; transition: opacity 0.4s, visibility 0.4s; }

.chanel { position: relative; height: 210px; padding: 32px 0 18px; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; color: #fff; }

