.subscr { display: inline-flex; align-items: center; }

.subscr__image { height: 60px; width: 60px; border-radius: 50%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; flex: 0 0 60px; min-width: 60px; }

